import React, {useState} from "react";
import { send } from 'emailjs-com';
import { useForm } from 'react-hook-form';
import Alert from 'react-bootstrap/Alert'
function Contact() {
  const [toSend, setToSend] = useState({
    full_name: '',
    phone: '',
    message: '',
    reply_to: '',
  });
  const [showDangerAlert, setshowDangerAlert] = useState(false);
  const [showSuccessAlert, setshowSuccessAlert] = useState(false);
  const [allowReset, setAllowReset] = useState(false);
  const [isDisabled, setIsDisabled] = useState(false);
  const [dangerAlertMessage, setDangerAlertMessage] = useState('');

  const resetForm = () => {
    document.getElementById('contact_form').reset();
    setIsDisabled(false);
  }
  const { register, handleSubmit, formState: { errors }} = useForm();
  const onSubmit = data => {
    setToSend({ ...toSend, full_name: data.full_name, phone: data.phone, reply_to: data.reply_to, message: data.message});
    setIsDisabled(true);
    setTimeout(function(){
      send(
        'service_0lna2rd',
        'template_hhkz9o8',
        toSend,
        'user_HmMjIvDG4EkkJt8qof2sX'
      )
        .then((response) => {
           return setshowSuccessAlert(true);
        })
        .catch((err) => {
          // console.log('FAILED...', err);
          setDangerAlertMessage('FAILED...');
          setIsDisabled(false);
          return setshowDangerAlert(true);
        });
      setAllowReset(true);
    }, 1000)
  };

  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Liên Hệ Với Chúng Tôi</h1>
      </div>

      <div className="container contact-div">
        <div className="row">
          <div className="col-md-6">
            <h3>CÔNG TY TNHH THƯƠNG MẠI PHÚC KHANG PLASTIC</h3>
            <div><b>Địa Chỉ:</b> Thửa đất số 999, Tờ bàn đồ số 142, Khu phố Bình Phước A, Phường Bình Chuẩn, Thành phố Thuận An, Tỉnh Bình Dương, Việt Nam.</div>
            <div><b>Số Điện Thoại:</b> 0989-558-209</div>
          </div>
          <div className="col-md-6">
            <p className="h5">Mời bạn điền vào mẫu thư liên lạc và gửi đi cho chúng tôi. Các chuyên viên tư vấn của chúng tôi sẽ trả lời bạn trong thời gian sớm nhất.</p>
            <form onSubmit={handleSubmit(onSubmit)} id="contact_form">
              <div className="form-group">
                <input
                  type="text"
                  className={errors.full_name ? "form-control is-invalid" : "form-control"}
                  placeholder="Họ & Tên*"
                  {...register("full_name",{ required: true, maxLength: 20})}
                />
                {errors.full_name?.type === 'required' && <span role="alert" className="invalid-feedback">Vui lòng nhập Họ và Tên</span>}
              </div>
              <div className="form-group pt-3">
                <input
                  type="text"
                  className={errors.full_name ? "form-control is-invalid" : "form-control"}
                  placeholder="Phone*"
                  name="phone"
                  {...register("phone", {required: true, pattern: /(([03+[2-9]|05+[6|8|9]|07+[0|6|7|8|9]|08+[1-9]|09+[1-4|6-9]]){3})+[0-9]{7}\b/g})}
                />
                {errors.phone?.type === 'required' && <span role="alert" className="invalid-feedback">Vui lòng nhập số điện thoại</span>}
              </div>
              <div className="form-group pt-3">
                <input
                  type="email"
                  className={errors.full_name ? "form-control is-invalid" : "form-control"}
                  placeholder="Email*"
                  name="reply_to"
                  {...register("reply_to", {required: true, pattern: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/})}
                />
                {errors.reply_to?.type === 'required' && <span role="alert" className="invalid-feedback">Vui lòng nhập Email</span>}
              </div>

              <div className="form-group pt-3">
                <textarea
                  className={errors.full_name ? "form-control is-invalid" : "form-control"}
                  rows="3"
                  placeholder="Nội dung*"
                  name="message"
                  {...register("message", {required: true})}
                ></textarea>
                {errors.message?.type === 'required' && <span role="alert"  className="invalid-feedback">Vui lòng nhập nội dung</span>}
              </div>
              <div className="col-12 pt-3">
                <button
                  className="btn btn-outline-primary"
                  type="submit"
                  disabled={isDisabled}
                >
                  Gửi Thư
                </button>
                <button show={allowReset} className="btn btn-outline-primary ms-3" onClick={resetForm}>Làm mới</button>
              </div>
              <Alert
              show={showDangerAlert}
              variant="danger"
              className="mt-3 ms-3"
            >
              {dangerAlertMessage}
            </Alert>
            <Alert
              show={showSuccessAlert}
              variant="success"
              className="mt-3 ms-3"
            >
              Cám ơn quý khách đã liện hệ với PhucKhang Plastic. Chúng tôi đã nhận được thư và sẽ trả lời quý khách trong thời gian sớm nhất.
            </Alert>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default Contact;
